/* CNN Header Layout Updated version on 04/10/2017 */
.wrap-header { padding-top: 60px; position: relative; }

.cnn-bar { position: absolute; top: 0; left: 0; display: block; height: auto; width: 100%; color: #ffffff; font-size: 10px; letter-spacing: 0.04em; z-index: 99999; font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif; font-weight: 400; height: 60px; }

.cnn-bar strong { font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif; font-weight: 700; }

.cnn-bar--fixed .cnn-bar__row.cnn-bar__row--gold { position: fixed; top: 0; left: 0; }

.cnn-bar--fixed .cnn-bar__logo { position: relative; height: 100%; }

.cnn-bar--fixed .cnn-bar__content-by { padding-left: 62px !important; }

.cnn-bar__wrapper { position: relative; width: 100%; height: 60px; }

.cnn-bar__row { display: flex; flex-direction: row; width: 100%; height: 30px; align-items: center; box-sizing: border-box; }

.cnn-bar__row--black { padding-left: 30px; background-color: #000000; }

.cnn-bar__row--gold { background-color: #b59343; position: relative; }

.cnn-bar__logo { position: fixed; top: 0; left: 0; width: auto; height: 30px; margin: 0; margin-right: auto; z-index: 10; }

.cnn-bar__logo svg, .cnn-bar__logo img { width: 30px; height: 100%; }

.cnn-bar__back-link { position: relative; display: flex; flex-direction: row; height: 100%; align-items: center; line-height: normal; color: #ffffff; text-decoration: none; padding-left: 8px; }

@media (min-width: 768px) { .cnn-bar__back-link { padding-left: 10px; } }

.cnn-bar__back-link:before { content: '\2039'; display: block; font-size: 1.6em; width: auto; height: 10px; line-height: 10px; margin-right: 2px; }

.cnn-bar__content-by { line-height: normal; position: absolute; width: 100%; text-align: center; box-sizing: border-box; left: 0; bottom: 50%; transform: translateY(50%); padding: 0 73px 0 30px; transition: 0.3s padding; }

@media (max-width: 768px) { .cnn-bar__content-by { padding: 0 62px 0 62px; } }

.cnn-bar__content-by strong, .cnn-bar__content-by a { font-weight: 700; color: white; text-decoration: none; }

.cnn-bar__share { position: relative; display: flex; flex-direction: row; border-left: 1px solid #ffffff; width: auto; height: 100%; font-size: 1.1em; margin-left: auto; }

.cnn-bar__share--active .cnn-bar__share-icons { pointer-events: all; }

@media (min-width: 768px) { .cnn-bar__share--active .cnn-bar__share-icons { max-width: 120px; } }

.cnn-bar__share--active .cnn-bar__share-icons .share-link { opacity: 1; }

.cnn-bar__share--active .cnn-bar__share-icons .share-link:nth-of-type(1) { transition-delay: 0ms; }

.cnn-bar__share--active .cnn-bar__share-icons .share-link:nth-of-type(2) { transition-delay: 50ms; }

.cnn-bar__share--active .cnn-bar__share-icons .share-link:nth-of-type(3) { transition-delay: 100ms; }

@media (min-width: 768px) { .cnn-bar__share--active .cnn-bar__share-icons .share-link:nth-of-type(1) { transition-delay: 125ms; }
  .cnn-bar__share--active .cnn-bar__share-icons .share-link:nth-of-type(2) { transition-delay: 175ms; }
  .cnn-bar__share--active .cnn-bar__share-icons .share-link:nth-of-type(3) { transition-delay: 225ms; } }

.cnn-bar__share-toggle { display: flex; align-items: center; width: auto; height: 100%; line-height: normal; text-transform: uppercase; cursor: pointer; padding: 0 10px; }

@media (min-width: 375px) { .cnn-bar__share-toggle { padding: 0 12px; } }

@media (min-width: 768px) { .cnn-bar__share-toggle { padding: 0 16px; } }

.cnn-bar__share-icons { display: flex; flex-direction: column; position: absolute; top: 100%; left: -1px; width: calc(100% + 1px); height: auto; max-width: none; pointer-events: none; }

@media (min-width: 768px) { .cnn-bar__share-icons { overflow: hidden; position: relative; top: auto; left: auto; flex-direction: row; max-width: 0; width: auto; height: 100%; transition: max-width 500ms cubic-bezier(0.19, 1, 0.22, 1); } }

.cnn-bar__share-icons .share-link { display: flex; width: 100%; height: 30px; opacity: 0; background-color: #b59343; border-top: 1px solid #fff; border-left: 1px solid #fff; transition: opacity 100ms ease; }

.cnn-bar__share-icons .share-link:last-of-type { border-bottom: 1px solid #fff; }

.cnn-bar__share-icons .share-link:nth-of-type(1) { transition-delay: 100ms; }

.cnn-bar__share-icons .share-link:nth-of-type(2) { transition-delay: 50ms; }

.cnn-bar__share-icons .share-link:nth-of-type(3) { transition-delay: 0ms; }

@media (min-width: 768px) { .cnn-bar__share-icons .share-link { width: auto; height: 100%; border: 0 !important; background-color: transparent; transition: opacity 200ms ease; }
  .cnn-bar__share-icons .share-link:last-of-type { margin-right: 16px; }
  .cnn-bar__share-icons .share-link:nth-of-type(1) { transition-delay: 0ms; }
  .cnn-bar__share-icons .share-link:nth-of-type(2) { transition-delay: 0ms; }
  .cnn-bar__share-icons .share-link:nth-of-type(3) { transition-delay: 0ms; } }

.cnn-bar__share-icons .share-link--twitter svg { height: 40%; width: 15px; }

.cnn-bar__share-icons .share-link--facebook svg { width: 6.6px; }

.cnn-bar__share-icons .share-link--linkedin svg { width: 13.2px; }

.cnn-bar__share-icons svg { margin: auto; width: auto; height: 44%; }

@media (min-width: 768px) { .cnn-bar__share-icons svg { margin: auto 10px; } }

.cnn-bar__share-icons svg path { fill: #fff; }

.cnn-footer { position: relative; background-color: black; color: #737373; font-size: 12px; line-height: 22px; font-weight: 400; padding: 44px 0 46px; }

.cnn-footer--ar { text-align: center; padding: 15px 0 6px; }

.cnn-footer--ar .cnn-footer__copyright { direction: rtl; }

.cnn-footer__logo { display: inline-block; vertical-align: top; }

.cnn-footer__top { margin-bottom: 20px; padding-bottom: 20px; border-bottom: 1px solid #262626; }

.cnn-footer__copyright { margin-top: 0; margin-bottom: 12px; }

.cnn-footer__menu { list-style: none; padding: 0; margin: 0; display: flex; flex-wrap: wrap; }

body:dir(rtl) .cnn-footer__menu { justify-content: center; }

.cnn-footer__menu li { margin-right: 11px; }

body:dir(rtl) .cnn-footer__menu li { position: relative; margin-left: 11px; }

