body:dir(rtl) .cnn-footer__menu li:last-child::after { display: none; }

.cnn-footer__menu a { color: inherit; transition: color .25s ease; }

body:dir(rtl) .cnn-footer__menu a { text-decoration: none; }

.cnn-footer__menu a:hover { color: white; }

.cnn-footer--es { background-color: white; color: #737373; text-align: center; padding: 64px 0 92px; }

@media (max-width: 699px) { .cnn-footer--es { padding: 64px 0 32px; } }

.cnn-footer--es .cnn-footer__menu { justify-content: center; color: #232323; }

.cnn-footer--es .cnn-footer__menu li { margin: 0 11px; position: relative; }

.cnn-footer--es .cnn-footer__menu li::after { content: ''; position: absolute; width: 1px; height: 14px; right: -12px; top: 4px; background-color: currentColor; }

.cnn-footer--es .cnn-footer__menu li:last-child::after { display: none; }

.cnn-footer--es .cnn-footer__menu li a { text-decoration: none; font-size: 15px; }

.cnn-footer--es .cnn-footer__menu li a:hover { color: black; }

.cnn-footer-es__logo { position: relative; margin-bottom: 35px; }

.cnn-footer-es__logo::before { content: ''; width: 100%; height: 1px; position: absolute; left: 0; top: 50%; background-color: #d8d8d8; }

.cnn-footer-es__logo img { width: 50px; height: 50px; display: block; margin: 0 auto; position: relative; }

.cnn-footer-es__copyright { line-height: 1.5; }

.cnn-footer-es__copyright a { color: inherit; text-decoration: none; }

.cnn-footer-es__copyright a:hover { color: black; }

.cnn-footer-es__share { margin: 28px 0 49px; }

.cnn-footer-es__share-links { display: flex; align-items: center; justify-content: center; }

.cnn-footer-es__share-link { width: 24px; height: 24px; display: block; margin: 0 19px; position: relative; }

.cnn-footer-es__share-link::after { content: ''; width: 1px; height: 50px; background-color: #979797; position: absolute; right: -18px; top: 50%; margin-top: -25px; }

.cnn-footer-es__share-link:last-child::after { display: none; }

.cnn-footer-es__share-link svg { width: 100%; height: 100%; display: block; }

.cnn-footer-es__label { color: #252525; display: block; margin-bottom: 25px; }

.cnn-footer-es__top { margin-top: 21px; font-size: 13px; letter-spacing: .16px; color: black; background-color: transparent; border: none; display: inline-flex; justify-content: center; align-items: center; cursor: pointer; }

.cnn-footer-es__top svg { width: 14px; height: 16px; margin-left: .35em; position: relative; top: -2px; }

