.hero-hub_wrapp { position: relative; width: 100%; position: absolute; top: 50%; left: 0; right: 0; margin-top: -39.3%; will-change: transform; }

.hero-hub_circle { position: absolute; top: 50%; left: 50%; margin-top: -126.8%; margin-left: -25vw; width: 50vw; height: 50vw; color: #C91609; z-index: 1; opacity: 1; }

